import { useState, useEffect, useRef } from "react";
import axios from "axios";
import "./TemplateEditor.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlus, faRightToBracket } from "@fortawesome/free-solid-svg-icons";

function TemplateEditor({ parsedData, onSave }) { //  onSave  props
  // State variables
  const [templateList, setTemplateList] = useState([]);
  const [selectedTemplate, setSelectedTemplate] = useState("");
  const [templateName, setTemplateName] = useState("");
  const [templateText, setTemplateText] = useState("");
  const [answerSets, setAnswerSets] = useState([]);
  const [selectedAnswerIndex, setSelectedAnswerIndex] = useState(0);
  const [answers, setAnswers] = useState({});
  const [status, setStatus] = useState("");
  const [previewHtml, setPreviewHtml] = useState("");
  const [sidebarVisible, setSidebarVisible] = useState(true);
  const [loading, setLoading] = useState(false);
const [templateContent, setTemplateContent] = useState("");
  const [jsonData, setJsonData] = useState("{}");

  // Ref for textarea
  const textareaRef = useRef(null);

  // API base URL -  consistent
  const API_BASE = "http://10.7.0.111:8002";

  // Helper function to get answer by key
  const getAnswerByKey = (key, fallback = "") => {
    return answers[key] ?? fallback;
  };

  // Prepare filled data
  const filledData = {
  company_name: getAnswerByKey("q29:  2.5.1: ?"),
  date: getAnswerByKey("q5:  1.1.2: ?"),
  location: getAnswerByKey("q3:  1.1.1: ?"),
  shareholder1: getAnswerByKey("q6:  1.1.3:   ?"),
  shareholder2: getAnswerByKey("q7:  1.1.3.1: ?"),
  shareholder3: getAnswerByKey("q8:  1.1.3.1.1: ?"),
  shareholder4: getAnswerByKey("q10:  1.1.3.3: "),
  purpose: getAnswerByKey("q30:  2.5.2: ?"),
  witness1: getAnswerByKey("q24:  2.3.1 ,"),
  witness2: getAnswerByKey("q27:  2.3.2 ,"),
  lawyer_info: getAnswerByKey("q28:  2.4.1 : [, , ]"),
  agreement_name: getAnswerByKey("q11: ") || "",  // fallback
  company_dissolution: getAnswerByKey("q32:  2.5.3.1: ?"),
  registered_office: getAnswerByKey("q35: : ?"),
  effective_date: getAnswerByKey("q16:   2.1.1.1.1: ?"),
  };

  // Initialize editableData with filledData
  const [editableData, setEditableData] = useState(filledData);

  // Update editableData when filledData changes
  useEffect(() => {
    setEditableData(filledData);
  }, [answers]);

  // Load template list
  useEffect(() => {
    axios.get(`${API_BASE}/templates`).then((res) => {
      setTemplateList(res.data);
    }).catch((err) => {
      console.error("Error loading templates:", err);
      setStatus(" template ");
    });
  }, []);

  // Load answer sets
  useEffect(() => {
    async function fetchAnswerSets() {
      try {
        const res = await axios.get(`${API_BASE}/answers/all`);
        setAnswerSets(res.data);
      } catch (err) {
        console.error("Error loading answer sets:", err);
        setStatus("");
      }
    }
    fetchAnswerSets();
  }, []);

  // Load selected answers when index changes
  useEffect(() => {
    async function fetchSelectedAnswers() {
      try {
        const res = await axios.get(`${API_BASE}/answers`, {
          params: { index: selectedAnswerIndex },
        });
        setAnswers(res.data);
      } catch (err) {
        console.error("Error loading selected answers:", err);
        setStatus("");
      }
    }
    fetchSelectedAnswers();
  }, [selectedAnswerIndex]);

  const handleChange = (key, value) => {
    setEditableData({ ...editableData, [key]: value });
  };

  const handleAddField = () => {
    const newKey = `new_key_${Date.now()}`; //  timestamp 
    setEditableData({ ...editableData, [newKey]: "question text" });
  };

  const handleRemoveField = (key) => {
    const newData = { ...editableData };
    delete newData[key];
    setEditableData(newData);
  };

  const handleSubmit = () => {
    if (onSave) {
      onSave(editableData);
      setStatus("");
    } else {
      setStatus(" -  onSave function");
    }
  };

  // Handle template selection
  const handleTemplateSelect = async (e) => {
    const name = e.target.value;
    setSelectedTemplate(name);
    setTemplateName(name);
    
    if (!name) {
      setTemplateText("");
      setPreviewHtml("");
      return;
    }

    try {
      const res = await axios.get(`${API_BASE}/template/${name}`);
      setTemplateText(res.data.content);
      setPreviewHtml("");
      setStatus("");
    } catch (err) {
      console.error("Error loading template:", err);
      setStatus(" template ");
    }
  };

  // Insert placeholder at cursor position
  const handleInsertPlaceholder = (placeholderKey) => {
    const textarea = textareaRef.current;
    if (!textarea) return;
    
    const insertText = `{{${placeholderKey}}}`;
    const start = textarea.selectionStart;
    const end = textarea.selectionEnd;
    const before = templateText.substring(0, start);
    const after = templateText.substring(end);
    const newText = before + insertText + after;
    
    setTemplateText(newText);

    // Set cursor position after insertion
    setTimeout(() => {
      textarea.focus();
      textarea.selectionStart = textarea.selectionEnd = start + insertText.length;
    }, 0);
  };

  // Preview template
  const handlePreview = async () => {
    if (!templateText.trim()) {
      setStatus(" template");
      return;
    }

    try {
      setStatus("...");
      const res = await axios.post(`${API_BASE}/preview`, {
        template: templateText,
        data: editableData, //  editableData  filledData
      });
      setPreviewHtml(res.data.rendered);
      setStatus("");
    } catch (err) {
      console.error("Error previewing:", err);
      setStatus(": " + (err.response?.data?.message || err.message));
    }
  };

  // Generate document
  const handleGenerate = async () => {
    if (!templateText.trim()) {
      setStatus(" template");
      return;
    }

    try {
      setStatus("...");
      const response = await axios.post(
        `${API_BASE}/generate`,
        { template: templateText, data: editableData }, //  editableData  filledData
        { responseType: "blob" }
      );
      
      const url = window.URL.createObjectURL(new Blob([response.data]));
      const link = document.createElement("a");
      link.href = url;
      link.setAttribute("download", `${selectedTemplate || "document"}.docx`);
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      window.URL.revokeObjectURL(url);
      
      setStatus("");
    } catch (err) {
      console.error("Error generating document:", err);
      setStatus(": " + (err.response?.data?.message || err.message));
    }
  };

  // Save template
  const handleSave = async () => {
    if (!templateName.trim()) {
      setStatus(" template");
      return;
    }

    if (!templateText.trim()) {
      setStatus(" template");
      return;
    }

    setStatus("...");
    try {
      const res = await axios.post(`${API_BASE}/template/save`, {
        name: templateName,
        content: templateText,
      });
      
      if (res.data.status === "success") {
        setStatus("");
        // Refresh template list
        const templatesRes = await axios.get(`${API_BASE}/templates`);
        setTemplateList(templatesRes.data);
      } else {
        setStatus(": " + (res.data.message || "Unknown error"));
      }
    } catch (err) {
      console.error("Error saving template:", err);
      setStatus(": " + (err.response?.data?.message || err.message));
    }
  };

    const placeholderLabels = {
    company_name: "",
    date: "",
    location: "",
    shareholder1: " 1",
    shareholder2: " 2",
    shareholder3: " 3",
    purpose: "",
    lawyer_info: "",
    agreement_name: "",
    company_dissolution: "",
    gaku: ""
    };

    const insertAroundSelection = (before, after = before) => {
    const textarea = textareaRef.current;
    const start = textarea.selectionStart;
    const end = textarea.selectionEnd;
    const selectedText = templateText.slice(start, end);
    const newText =
      templateText.slice(0, start) +
      before +
      selectedText +
      after +
      templateText.slice(end);
    setTemplateText(newText);

    //  cursor  format
    setTimeout(() => {
      textarea.focus();
      textarea.selectionStart = start + before.length;
      textarea.selectionEnd = end + before.length;
    }, 0);
  };

    //  generate PDF
    const generatePdf = async () => {
  setLoading(true);

  const data = editableData;

  try {
    const response = await axios.post(
      "http://10.7.0.111:8002/generate_pdf",
      {
        template: templateContent,
        data,
        answer_id: 0
      },
      { responseType: "blob" }
    );

    const url = window.URL.createObjectURL(new Blob([response.data], { type: "application/pdf" }));
    const link = document.createElement("a");
    link.href = url;
    link.setAttribute("download", "generated.pdf");
    document.body.appendChild(link);
    link.click();
    link.remove();
  } catch (error) {
    alert(" PDF");
    console.error(error);
  }

  setLoading(false);
};


  return (
    <div className="template-editor">
      <h2>  Template</h2>

      <div className="controls">
        {/* Toggle Sidebar Button */}
        <button 
          onClick={() => setSidebarVisible(!sidebarVisible)} 
          style={{ marginLeft: 10 }}
          title={sidebarVisible ? " Sidebar" : " Sidebar"}
        >
          {sidebarVisible ? (
            <>
                <FontAwesomeIcon icon={faRightToBracket} /> 
            </>
            ) : (
            <>
                <FontAwesomeIcon icon={faRightToBracket} className="fa-flip-horizontal" /> 
            </>
            )}
        </button>

        {/* Template Selection */}
        <select style={{fontFamily: "'Prompt', sans-serif"}}onChange={handleTemplateSelect} value={selectedTemplate}>
          <option value="">--  Template --</option>
          {templateList.map((tpl) => (
            <option key={tpl} value={tpl}>{tpl}</option>
          ))}
        </select>

        {/* Answer Set Selection */}
        <select
          value={selectedAnswerIndex}
          onChange={(e) => setSelectedAnswerIndex(Number(e.target.value))}
          style={{ marginLeft: 10 }}
        >
          {answerSets.map((_, idx) => (
            <option key={idx} value={idx}>Answer Set {idx + 1}</option>
          ))}
        </select>
      </div>

      <div className={`editor-container ${!sidebarVisible ? 'sidebar-hidden' : ''}`}>
        {/* Sidebar with Placeholders */}
        {sidebarVisible && (
          <div className="sidebar">
            <h3>  Placeholders</h3>
            <div className="placeholder-list">
              {Object.keys(editableData).map((key) => (
                <div
                  key={key}
                  className="placeholder-item"
                  onClick={() => handleInsertPlaceholder(key)}
                  title={` {{${key}}}`}
                >
                 <div className="placeholder-preview">
                    <strong>{placeholderLabels[key] || key}</strong>: {editableData[key]}
                  </div>
                  <code>{`{{${key}}}`}</code>
                </div>
              ))}
            </div>
          </div>
        )}

        {/* Main Editor */}
        <div className="editor-main">
          {/* Template Name Input */}
          <div style={{ marginTop: 10, marginBottom: 10 }}>
            <label> Template: </label>        
            <input
              type="text"
              value={templateName}
              onChange={(e) => setTemplateName(e.target.value)}
              placeholder=" template..."
              style={{ marginLeft: 10, padding: 5, width: 300, fontFamily: "'Prompt', sans-serif" }}
            />
        {/* <div className="btn-tool">
            <button onClick={() => insertAroundSelection("**")}>B</button>
            <button onClick={() => insertAroundSelection("_")}>I</button>
            <button onClick={() => insertAroundSelection("<u>", "</u>")}>U</button>
            <button onClick={() => insertAroundSelection("<mark>", "</mark>")}>Highlight</button>
        </div> */}
          </div>

          {/* Template Content Textarea */}
          <textarea
            ref={textareaRef}
            rows="15"
            style={{ width: "99%", marginTop: 10, fontFamily: "'Prompt', sans-serif" }}
            value={templateText}
            onChange={(e) => {
            setTemplateText(e.target.value);
            setTemplateContent(e.target.value); // <- 
            }}
            placeholder=" template ...  {{placeholder}} "
          />

          {/* Action Buttons */}
          <div style={{ marginTop: 10 }}>
            <button onClick={handlePreview}> Preview</button>
            <button onClick={handleGenerate} style={{ marginLeft: 10 }}>
               
            </button>
                  {/* <button
                        onClick={generatePdf}
                        disabled={loading || !templateContent}
                    >
                    {loading ? " PDF..." : "Generate PDF"}
                    
                </button> */}
            <button onClick={handleSave} style={{ marginLeft: 10 }}>
               Save Template
            </button>
          </div>
        </div>
      </div>

      {/* Preview Section */}
      {previewHtml && (
        <div className="preview-section">
          <h3> :</h3>
          <div 
            className="preview-content"
            dangerouslySetInnerHTML={{ __html: previewHtml }} 
          />
        </div>
      )}
    </div>
  );
}

export default TemplateEditor;