import React, { useState } from 'react';
import './test2.css'
const DocumentTemplateManager = () => {
  const [jsonData, setJsonData] = useState('');
  const [parsedData, setParsedData] = useState(null);
  const [error, setError] = useState('');

  // 
  const sampleData = `{
    "Answer 8": {
      "q1:     ?": "",
      "q2: ?": "",
      "q3: ?": "2025-08-29",
      "q4:   ?": "",
      "q5: ?": "",
      "q6: ?": "",
      "q7: 2.  (ENTRY CLAUSE)": "",
      "q9: ?": "2025-08-14",
      "q10: ?": "",
      "q11:  ( \\"\\" )": "",
      "q13:    ()?": "",
      "q14: ?": "",
      "q15: ?": "",
      "q17: ?": "",
      "q18: ?": "",
      "q22: ?": "",
      "q25:  [, , ] ": " ",
      "q26: ?": "",
      "q27: ?": "",
      "q28: ?": "",
      "q30: ?": "2025-08-30"
    }
  }`;

  //  JSON
  const parseJsonData = () => {
    try {
      let parsed = JSON.parse(jsonData || sampleData);
      
      //  array  array
      if (!Array.isArray(parsed)) {
        parsed = [parsed];
      }
      
      setParsedData(parsed);
      setError('');
    } catch (err) {
      setError(' JSON  ');
      setParsedData(null);
    }
  };

  // 
  const getAnswerByKey = (data, searchKey) => {
    if (!data || !Array.isArray(data)) return '';
    
    for (const item of data) {
      for (const answerKey in item) {
        const answers = item[answerKey];
        for (const question in answers) {
          if (question.includes(searchKey) || question.toLowerCase().includes(searchKey.toLowerCase())) {
            return answers[question];
          }
        }
      }
    }
    return '';
  };

  // 
  const generateDocument = () => {
    if (!parsedData) return '';

    const companyName = getAnswerByKey(parsedData, '');
    const signDate = getAnswerByKey(parsedData, '');
    const signLocation = getAnswerByKey(parsedData, '');
    const shareholder1 = getAnswerByKey(parsedData, '');
    const shareholder2 = getAnswerByKey(parsedData, '');
    const shareholder3 = getAnswerByKey(parsedData, '');
    const companyPurpose = getAnswerByKey(parsedData, '');
    const lawyerInfo = getAnswerByKey(parsedData, '[, , ]');
    const agreementName = getAnswerByKey(parsedData, '');
    const companyyub = getAnswerByKey(parsedData, '');

    return `

 ${companyName} 

: ${signDate}
: ${signLocation}

${agreementName}:
${shareholder1 ? `1. ${shareholder1}` : ''}
${shareholder2 ? `2. ${shareholder2}` : ''}  
${shareholder3 ? `3. ${shareholder3}` : ''}

: ${companyPurpose}

: ${lawyerInfo}

 ${companyName}
 ${signDate}
 ${companyyub}
`;
  };



  return (
    <>
      <div className="document-container">
      <h1 className="document-header"> Template </h1>
      
      <div className="document-section">
        <h2 className="section-title">1.  JSON</h2>
        <textarea
          className="json-textarea"
          placeholder=" JSON  "
          value={jsonData}
          onChange={(e) => setJsonData(e.target.value)}
        />
        <div className="button-group">
          <button className="btn-primary" onClick={parseJsonData}>
            
          </button>
          <button 
            className="btn-secondary" 
            onClick={() => {
              setJsonData(sampleData);
              // Auto parse 
              setTimeout(() => {
                parseJsonData();
              }, 100);
            }}
          >
            
          </button>
        </div>
        {error && <div className="error-message">{error}</div>}
      </div>

      {parsedData && (
        <>
          <div className="document-section">
            <h2 className="section-title">2. </h2>
            <table className="data-table">
              <thead>
                <tr>
                  <th className="table-header"></th>
                  <th className="table-header"></th>
                </tr>
              </thead>
              <tbody>
                {parsedData.map((item, index) => (
                  Object.entries(item).map(([answerKey, answers]) => (
                    Object.entries(answers).map(([question, answer], qIndex) => (
                      <tr key={`${index}-${qIndex}`}>
                        <td className="table-cell">{question}</td>
                        <td className="table-cell">{answer}</td>
                      </tr>
                    ))
                  ))
                ))}
              </tbody>
            </table>
          </div>

          <div className="document-section">
            <h2 className="section-title">3. </h2>
            <div className="document-preview">
              {generateDocument()}
            </div>
            <div className="button-group">
              <button 
                className="btn-primary"
                onClick={() => {
                  const doc = generateDocument();
                  const blob = new Blob([doc], { type: 'text/plain;charset=utf-8' });
                  const url = URL.createObjectURL(blob);
                  const a = document.createElement('a');
                  a.href = url;
                  a.download = 'agreement.txt';
                  a.click();
                  URL.revokeObjectURL(url);
                }}
              >
                
              </button>
              <button 
                className="btn-secondary"
                onClick={() => {
                  navigator.clipboard.writeText(generateDocument());
                  alert('!');
                }}
              >
                
              </button>
            </div>
          </div>
        </>
      )}
    </div>
    </>
  );
};

export default DocumentTemplateManager;