import React, { useEffect, useState } from "react";
import axios from "axios";
import './Test.css'

const DownloadDocx = () => {
  const [answerKeys, setAnswerKeys] = useState([]);
  const [selectedKey, setSelectedKey] = useState("");

  useEffect(() => {
    axios.get("http://10.7.0.111:8001/list-answers")
      .then(res => setAnswerKeys(res.data.keys))
      .catch(err => console.error(err));
  }, []);

  const handleDownload = () => {
    if (!selectedKey) return;

    const url = `http://10.7.0.111:8001/generate-docx?answer_key=${encodeURIComponent(selectedKey)}`;
    window.open(url, "_blank"); //  <a download>
  };

  return (
    <div className="container-download">
      <h3></h3>
      <select value={selectedKey} onChange={(e) => setSelectedKey(e.target.value)}>
        <option value="">--  --</option>
        {answerKeys.map((key, idx) => (
          <option key={idx} value={key}>{key}</option>
        ))}
      </select>
      <button onClick={handleDownload} disabled={!selectedKey}>
         .docx
      </button>
    </div>
  );
};

export default DownloadDocx;
